<script>
import {
  getData,
  opthionList,
  CompanySwitchClear,
  tableResize //明细表去除页面滚动条
} from "common/utils/public_function/commonUtils";
import { mapActions, mapGetters } from "vuex";
import getAreaList from "common/components/public/getAreaList";
export default {
  name: 'instorageGetThreeOutStorageDetailGetThreeOutStorageDetail',
  data() {
    return {
      timeValue1: [],
      defaultArea: [0], //默认的区域id
      outerVisible: false,
      loading:false,
      TableData: {
        tableData: [],
        allList: {},
        total: 10
      },
      is_shop: 0,
      tableHeight: 600
    };
  },
  components: {
    getAreaList //区域列表
  },
  computed: {
    ...mapGetters(["getThreeOutStorageDetailIndexGet"])
  },
  methods: {
    ...mapActions(["getThreeOutStorageDetailIndexACT"]),
    allocationChange(val) {
      if (val != "") {
        this.params.allocation_name = this.allocationInventoryList.filter(
          obj => obj.allocation_id === val
        )[0].allocation_name;
      }
    },
    ckChange(val) {
      if (val != "") {
        this.params.warehouse_name = this.warehouseList.filter(
          obj => obj.warehouse_id === val
        )[0].warehouse_name;
      }
      this.$set(this.params, "allocation_id", "");
    },
    businessIdChange(val) {
      if (val != "") {
        this.params.business_name = this.employeeList.filter(
          obj => obj.employee_id === val
        )[0].employee_name;
      }
    },
    customerListChange(val) {
      if (val) {
        this.params.customer_name = this.customerList.filter(
          obj => obj.customer_id === val
        )[0].customer_name;
      }
    },
    departmentChange(val) {
      if (val != "") {
        this.params.department_name = this.departmentList.filter(
          obj => obj.department_id === val
        )[0].department_name;
      }
      this.$set(this.params, "business_id", "");
    },
    exportOrder(scheduleType) {
      // 导出库存收发明细表导出URL
      let s = Object.assign(this.params, {
        export_type: scheduleType
      });
      this.$axMethod.exportThreeOutStorageDetailUrl(s).then(response => {
        if (response.data.code == 200) {
          window.location.href = response.data.data.url;
        }
      });
    },
    getSummaries(param) {
      const { columns, data } = param;
      const sums = [];
      columns.forEach((column, index) => {
        if (index === 0) {
          sums[index] = "页小计";
          return;
        }
        const values = data.map(item => Number(item[column.property]));
        if (!values.every(value => isNaN(value))) {
          sums[index] = values.reduce((prev, curr) => {
            const value = Number(curr);
            if (!isNaN(value)) {
              return prev + curr;
            } else {
              return prev;
            }
          }, 0);
          if (
            index === 28 ||
            index === 25 ||
            index === 24 ||
            index === 26 ||
            index === 27
          ) {
            sums[index] = parseFloat(sums[index])
              .toFixed(2)
              .toString()
              .replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1" + ",");
          } else if (index === 27) {
            sums[
              index
            ] = this.TableData.allList.page_settlement_price
              .toString()
              .replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1" + ",");
          } else if (index == 17) {
            sums[index] = sums[index];
          } else {
            sums[index] = "";
          }
        } else {
          sums[index] = "";
        }
      });

      return sums;
    },
    getprovinceFocus() {
      this.$common.options("getAreaListOption", this.province, {
        level: 2
      });
    },
    companyChange(val) {
      this.$set(this.params, "warehouse_id", "");
      if (val != "") {
        this.params.company_name = this.companyList.filter(
          obj => obj.company_id === val
        )[0].company_name;
      }
      CompanySwitchClear(this, this.params);
    },
    changeDefaultArea(addressId) {
      this.params.cityList = addressId.join(",");
      this.params.province_id = addressId[0];
      this.params.city_id = addressId[1];
      this.params.area_id = addressId[2];
    },
    onReset() {
      /** 重置时 如需保存默认参数，则： */
      this.defaultArea = [0];
      this.$set(this, "params", {
        auditor_status: 0
      });
      this.timeValue = [];
      this.timeValue1 = [];
      this.getThreeOutStorageDetailIndexACT();
      this.$set(this, "searchParams", {
        params: this.params,
        TableData: this.TableData, //列表数据
        API: "getThreeOutStorageDetail", //接口名
        vm: this //传递的this
      });
      this.onSearch();
    },
    onSearch() {
      //查询
      if (this.timeValue) {
        this.params.start_time = this.timeValue[0];
        this.params.end_time = this.timeValue[1];
      } else {
        this.params.start_time = this.params.end_time = "";
      }
      if (this.timeValue1) {
        this.params.auditor_start_time = this.timeValue1[0];
        this.params.auditor_end_time = this.timeValue1[1];
      } else {
        this.params.auditor_start_time = this.params.auditor_end_time = "";
      }
      Object.assign(this.searchParams.params, this.params, {
        page: 1
      });
      this.getThreeOutStorageDetailIndexACT({
        goPage: this.goPage,
        params: this.params,
        pageSize: this.pageSize
      });
      getData(this.searchParams, 2);
    },
    handleSizeChange(val) {
      Object.assign(this.searchParams.params, {
        per_page: val,
        page: 1
      });
      getData(this.searchParams, 2);
    },
    handleCurrentChange(val) {
      Object.assign(this.searchParams.params, {
        page: val
      });
      getData(this.searchParams, 2);
    }
  },
  mounted() {
    if (
      this.getThreeOutStorageDetailIndexGet &&
      this.getThreeOutStorageDetailIndexGet.params
    ) {
      if (this.getThreeOutStorageDetailIndexGet.params.start_time) {
        const {
          start_time,
          end_time
        } = this.getThreeOutStorageDetailIndexGet.params;
        this.timeValue = this.timeValue.concat([start_time, end_time]);
      }
      if (this.getThreeOutStorageDetailIndexGet.params.auditor_start_time) {
        const {
          auditor_start_time,
          auditor_end_time
        } = this.getThreeOutStorageDetailIndexGet.params;
        this.timeValue1 = this.timeValue1.concat([
          auditor_start_time,
          auditor_end_time
        ]);
      }
      let objMessage = this.getThreeOutStorageDetailIndexGet.params;
      this.defaultArea = [
        objMessage.province_id,
        objMessage.city_id,
        objMessage.area_id
      ];
      Object.assign(this.getThreeOutStorageDetailIndexGet.params, {
        employee_id: this.getThreeOutStorageDetailIndexGet.params.business_id,
        employee_name: this.getThreeOutStorageDetailIndexGet.params
          .business_name
      });
      this.$set(this, "params", this.getThreeOutStorageDetailIndexGet.params);
      opthionList(this, this.getThreeOutStorageDetailIndexGet.params);
    }
    // this.$set(this, "params", {
    //   auditor_status: 0
    // });
    this.$set(this, "searchParams", {
      params: this.params, //1默认带的参数
      TableData: this.TableData, //列表数据
      API: "getThreeOutStorageDetail", //接口名
      vm: this //传递的this
    });
    getData(this.searchParams, 2, tableResize.bind(this));

    //取消单据右侧滚动条
    window.onresize = tableResize.bind(this);

    const elTable1 = this.$refs.table1.$el.getElementsByClassName(
      "el-table__body-wrapper"
    )[0];
    const elTable2 = this.$refs.table_sum;
    elTable1.addEventListener("scroll", function() {
      elTable2.scrollLeft = this.scrollLeft;
    });
  }
};
</script>
